"use client"

import { Button } from "@/components/ui/button"
import { Calendar as CalendarIcon, ChevronLeft, ChevronRight } from "lucide-react"
import { format, addDays, subDays, isToday, addWeeks, subWeeks, startOfWeek, endOfWeek } from "date-fns"
import { useRouter, useSearchParams } from "next/navigation"
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover"
import { Calendar } from "@/components/ui/calendar"
import { useState } from "react"
import { cn } from "@/lib/utils"

export function WeekNavigator() {
    const router = useRouter()
    const searchParams = useSearchParams()

    const view = searchParams.get("view") || "day"
    const dateParam = searchParams.get("date")
    const currentDate = dateParam ? new Date(dateParam) : new Date()

    const [calendarOpen, setCalendarOpen] = useState(false)

    const navigate = (date: Date, newView?: string) => {
        const dateString = format(date, "yyyy-MM-dd")
        const v = newView || view
        router.push(`/time?date=${dateString}&view=${v}`)
        setCalendarOpen(false)
    }

    const previous = () => {
        if (view === 'week') {
            navigate(subWeeks(currentDate, 1))
        } else {
            navigate(subDays(currentDate, 1))
        }
    }

    const next = () => {
        if (view === 'week') {
            navigate(addWeeks(currentDate, 1))
        } else {
            navigate(addDays(currentDate, 1))
        }
    }

    const goToToday = () => navigate(new Date())

    // Calculate week range for display if in week view
    const weekStart = startOfWeek(currentDate, { weekStartsOn: 1 })
    const weekEnd = endOfWeek(currentDate, { weekStartsOn: 1 })

    return (
        <div className="flex flex-col sm:flex-row items-center gap-4 w-full">
            <div className="flex items-center gap-2">
                <div className="flex items-center p-1 bg-muted/20 border rounded-lg">
                    <Button
                        variant={view === "day" ? "secondary" : "ghost"}
                        size="sm"
                        onClick={() => navigate(currentDate, "day")}
                        className="text-xs h-7"
                    >
                        Day
                    </Button>
                    <Button
                        variant={view === "week" ? "secondary" : "ghost"}
                        size="sm"
                        onClick={() => navigate(currentDate, "week")}
                        className="text-xs h-7"
                    >
                        Week
                    </Button>
                </div>

                <div className="h-4 w-px bg-border mx-1" />

                <div className="flex items-center space-x-1 border rounded-md p-0.5 bg-background shadow-sm">
                    <Button variant="ghost" size="icon" className="h-7 w-7" onClick={previous}>
                        <ChevronLeft className="h-4 w-4" />
                    </Button>
                    <Button variant="ghost" size="icon" className="h-7 w-7" onClick={next}>
                        <ChevronRight className="h-4 w-4" />
                    </Button>
                </div>

                <div className="flex items-center gap-1 ml-1">
                    <Popover open={calendarOpen} onOpenChange={setCalendarOpen}>
                        <PopoverTrigger asChild>
                            <Button variant="outline" size="icon" className={cn("h-8 w-8", calendarOpen && "bg-muted")}>
                                <CalendarIcon className="h-4 w-4" />
                            </Button>
                        </PopoverTrigger>
                        <PopoverContent className="w-auto p-0" align="start">
                            <Calendar
                                mode="single"
                                selected={currentDate}
                                onSelect={(d) => d && navigate(d)}
                                initialFocus
                            />
                        </PopoverContent>
                    </Popover>

                    {!isToday(currentDate) && (
                        <Button variant="ghost" size="sm" onClick={goToToday} className="text-xs h-8">
                            Today
                        </Button>
                    )}
                </div>
            </div>

            <h2 className="text-lg font-semibold flex-1 text-center sm:text-left">
                {view === 'day' ? (
                    <>
                        {isToday(currentDate) ? "Today: " : ""}{format(currentDate, "EEEE, d MMM")}
                    </>
                ) : (
                    <>
                        Week of {format(weekStart, "MMM d")} - {format(weekEnd, "MMM d, yyyy")}
                    </>
                )}
            </h2>
        </div>
    )
}
