"use client"

import { useState } from "react"
import { TimeEntryList } from "@/components/work/time-entry-list"
import { TimeLogDialog } from "@/components/work/time-log-dialog" // Reusing this, will need to update it to support "editing" mode
import { useRouter } from "next/navigation"

import { TimeEntryWeekView } from "./time-entry-week-view"

export function TimeEntryListWrapper({ entries, tasks, projects, view = "day", date }: { entries: any[], tasks: any[], projects: any[], view?: string, date?: string }) {
    const [editingEntry, setEditingEntry] = useState<any | null>(null)
    const router = useRouter()

    return (
        <>
            {view === 'week' ? (
                <TimeEntryWeekView
                    entries={entries}
                    onEdit={setEditingEntry}
                    date={date ? new Date(date) : new Date()}
                />
            ) : (
                <TimeEntryList
                    entries={entries}
                    onEdit={(entry) => setEditingEntry(entry)}
                />
            )}

            {editingEntry && (
                <TimeLogDialog
                    open={!!editingEntry}
                    onOpenChange={(open) => !open && setEditingEntry(null)}
                    tasks={tasks}
                    projects={projects}
                    defaultDate={new Date(editingEntry.startedAt).toISOString().split("T")[0]}
                    entryToEdit={editingEntry}
                    onComplete={() => {
                        setEditingEntry(null)
                        router.refresh()
                    }}
                />
            )}
        </>
    )
}
