"use client"

import { Button } from "@/components/ui/button"
import { Play, Edit, Trash2, Folder, Briefcase, FileText } from "lucide-react"
import { format } from "date-fns"
// In real app, would use specific types
import { deleteTimeEntry } from "@/actions/time-actions"

interface TimeEntryListProps {
    entries: any[]
    onEdit: (entry: any) => void
}

export function TimeEntryList({ entries, onEdit }: TimeEntryListProps) {
    if (entries.length === 0) {
        return (
            <div className="flex flex-col items-center justify-center p-12 border-2 border-dashed rounded-lg bg-slate-50 dark:bg-slate-900/50 text-muted-foreground">
                <Briefcase className="h-12 w-12 mb-4 opacity-20" />
                <h3 className="text-lg font-medium">No time logged</h3>
                <p>Click "Track Time" to add an entry for this day.</p>
            </div>
        )
    }

    // Group by Project (Optional improvement, Harvest does this flat-ish or grouped)
    // Let's keep it flat list for now, sorted descending by start time usually.

    return (
        <div className="border rounded-lg shadow-sm bg-background divide-y">
            {entries.map((entry) => (
                <div key={entry.id} className="group flex items-center justify-between p-4 hover:bg-muted/30 transition-colors">
                    <div className="flex-1 min-w-0 pr-4">
                        <div className="flex items-center gap-2 mb-1">
                            <span className="font-semibold truncate">
                                {entry.project?.name || "No Project"}
                            </span>
                            {entry.workItem && (
                                <>
                                    <span className="text-muted-foreground">•</span>
                                    <span className="text-sm text-indigo-600 dark:text-indigo-400 font-medium truncate">
                                        {entry.workItem.title}
                                    </span>
                                </>
                            )}
                        </div>
                        <div className="text-sm text-muted-foreground line-clamp-2">
                            {entry.notes || <span className="italic opacity-50">No notes</span>}
                        </div>
                    </div>

                    <div className="flex items-center gap-6">
                        <div className="text-right">
                            <div className="text-2xl font-semibold tracking-tight">
                                {formatDuration(entry.durationSeconds)}
                            </div>
                            {/* <div className="text-xs text-muted-foreground">
                                {format(new Date(entry.startedAt), "h:mm a")} - {format(new Date(entry.endedAt || entry.createdAt), "h:mm a")}
                             </div> 
                             Harvest usually just shows total hours for the entry
                             */}
                        </div>

                        <div className="flex items-center gap-1 opacity-0 group-hover:opacity-100 transition-opacity">
                            <Button variant="ghost" size="sm" onClick={() => onEdit(entry)}>
                                <Edit className="h-4 w-4" />
                            </Button>
                            {/* "Start" button - for now just UI, functionality needs duplication logic */}
                            <Button variant="outline" size="sm" className="hidden sm:flex" disabled>
                                <Play className="h-4 w-4" />
                            </Button>
                        </div>
                    </div>
                </div>
            ))}

            <div className="p-4 bg-muted/20 flex justify-between items-center font-bold">
                <span>Day Total</span>
                <span className="text-xl">
                    {formatDuration(entries.reduce((acc, e) => acc + e.durationSeconds, 0))}
                </span>
            </div>
        </div>
    )
}

function formatDuration(seconds: number) {
    const hours = Math.floor(seconds / 3600)
    const minutes = Math.floor((seconds % 3600) / 60)
    return `${hours}:${minutes.toString().padStart(2, '0')}`
}
